import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';
import { ColEditDelete } from 'components/common';
import { BtnAddNew } from 'components/common/button';
import { Card, Row, Table, Col, message } from 'antd';
import { getListTown, deleteTown } from 'services/town';

class Town extends Component {
  state = {
    dataTable: [],
    loading: false,
    pagination: {},
    IsPagingEnabled: true,
  };

  componentDidMount() {
    this.getListTowntable();
  }

  getListTowntable = (page, isPagingEnabled) => {
    this.setState({
      loading: true,
    });
    getListTown({ page, isPagingEnabled })
      .then(res => {
        const pagination = { ...this.state.pagination };
        pagination.total = res.data.totalResults;
        this.setState({
          dataTable: res.data.items,
          loading: false,
          pagination,
        });
      })
      .catch(error => {
        console.log('====>', error);
        this.setState({
          loading: false,
        });
      });
  };

  handleTableChange = pagination => {
    const { IsPagingEnabled } = this.state;
    this.getListTowntable(pagination.current, IsPagingEnabled);
  };

  confirmDelete = e => {
    this.setState({
      loading: true,
    });
    deleteTown(e)
      .then(() => {
        message.success('Xa tnh thnh cng');
        this.getListTowntable();
        this.setState({
          loading: false,
        });
      })
      .catch(error => {
        this.setState({
          loading: false,
        });
        console.log('res', error);
        message.error(error.response.data.message);
      });
  };

  cancel = () => {};

  renderTable = () => {
    const { dataTable } = this.state;
    const columns = [
      {
        title: 'K hiu',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tn tnh',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Tn ting anh',
        dataIndex: 'labelInEnglish',
        key: 'labelInEnglish',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) =>
          dataTable.length >= 1 ? (
            <Fragment>
              <ColEditDelete
                linkEdit={{
                  to: {
                    pathname: routeConstant.DANH_MUC__TUYEN_SINH__TINH__NHAP_DU_LIEU,
                    search: `?id=${record.id}`,
                  },
                }}
                onConfirmDelete={() => this.confirmDelete(record.id)}
              />
            </Fragment>
          ) : null,
      },
    ];
    return (
      <Table
        loading={this.state.loading}
        bordered
        dataSource={this.state.dataTable}
        columns={columns}
        pagination={this.state.pagination}
        onChange={this.handleTableChange}
        rowKey="id"
      />
    );
  };

  render() {
    return (
      <Card title="Danh mc tnh">
        <Row>
          <Col span={24}>
            <Link to={routeConstant.DANH_MUC__TUYEN_SINH__TINH__NHAP_DU_LIEU}>
              <BtnAddNew />
            </Link>
          </Col>
        </Row>
        <Row>
          <Col span={24}>{this.renderTable()}</Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <Town />
  </Page>
);



